// import _ from 'lodash'
import * as THREE from 'three'
import axios from 'axios'

import {
	section3dObjects,
	setupThisRenderer as simRobotResize,
	poseLerp,
	scene as simScene,
	wristAffector3dObject,
} from '../components/SimRobot/SimRobot'
import Sixi2 from '../model/Sixi2'
import {currentPose as historyCurrentPose} from '../components/HistoryTimeline/HistoryTimeline'
import {currentPose as sequenceCurrentPose} from '../components/SequenceControls/SequenceControls'

export let currentState={}
export let prevPose
export let prevBottomExpanded
export const mainReducer=(state,[actionType,data])=>{
	currentState=reducer(state,actionType,data)
	if(currentState.isPolling==='false') currentState.isPolling=false
	if(currentState.isPolling==='true') currentState.isPolling=true
	if(prevBottomExpanded !== (state.showHistoryTimeline || state.showSequenceControl)){
		console.log('Bottom drawer changed')
		window.setTimeout(simRobotResize,0)
		prevBottomExpanded=state.showHistoryTimeline || state.showSequenceControl
	}
	if(actionType==='SET_SIM_ANGLES'){
		currentState.historySeekPos=state.poseHistory.length
		currentState.sequenceSeekPos=state.sequencePoses.length
	}
	if(actionType==='HISTORY_SEEK')
		currentState.sequenceSeekPos=state.sequencePoses.length
	if(actionType==='SEQ_SEEK')
		currentState.historySeekPos=state.poseHistory.length

	// console.log(data,actionType,currentState)
	return currentState
}

// RULE: Do not use currentState below
function reducer(state,actionType,data){
	let pose
	let a
	let b
	let c
	let p
	let alpha
	let pos

	switch(actionType){
		case 'SET_STATE':
			return {...state,...data}
		case 'SET_SIM_ANGLES':
			for(let i in data){
				data[i]=THREE.Math.clamp(+data[i].toFixed(3),Sixi2[i].angles.min,Sixi2[i].angles.max)
				section3dObjects[i].rotateSection(data[i])
			}
			simScene.updateMatrixWorld()
			pos=new THREE.Vector3()
			section3dObjects['v'].getWorldPosition(pos)
			wristAffector3dObject.position.set(...pos.toArray())
			return {...state,simPose: data}
		case 'GO':
			// TODO: check
			if(state.online) axios.put(`https://${state.IP}:7878/goto`,state.simPose)
			pose=JSON.stringify(state.simPose)
			if(prevPose!==pose){
				state.poseHistory.push(pose)
				state.historySeekPos=state.poseHistory.length
				prevPose=pose
			}
			return {...state}
		case 'HISTORY_SEEK':
			if(isNaN(+data)) data=0
			data=THREE.Math.clamp(+data,0,state.poseHistory.length)
			a=Math.floor(data)
			b=Math.ceil(data)
			alpha=+((data%1).toFixed(state.historySeekbarPrecision))
			// const alpha=data%1
			c=JSON.parse(historyCurrentPose)
			a=a>=state.poseHistory.length?c:JSON.parse(state.poseHistory[a])
			b=b>=state.poseHistory.length?c:JSON.parse(state.poseHistory[b])
			p=poseLerp(a,b,alpha)
			if(state.historyLockHand) p['t']=c['t']
			return reducer({...state,historySeekPos: data},'SET_SIM_ANGLES',p)
		case 'HISTORY_LOCK_HAND':
			return reducer({...state,historyLockHand: data},'HISTORY_SEEK',state.historySeekPos)
		case 'SEQ_ADD':
			pose=JSON.stringify(state.simPose)
			if(state.sequencePoses.length && state.sequencePoses[state.sequencePoses.length-1]===pose)
				return state
			state.sequencePoses.push(pose)
			state.sequenceSeekPos=state.sequencePoses.length
			return {...state}
		case 'SEQ_SEEK':
			if(isNaN(+data)) data=0
			data=THREE.Math.clamp(+data,0,state.sequencePoses.length)
			a=Math.floor(data)
			b=Math.ceil(data)
			alpha=+((data%1).toFixed(2))
			// const alpha=data%1
			c=JSON.parse(sequenceCurrentPose)
			a=a>=state.sequencePoses.length?c:JSON.parse(state.sequencePoses[a])
			b=b>=state.sequencePoses.length?c:JSON.parse(state.sequencePoses[b])
			p=poseLerp(a,b,alpha)
			return reducer({...state,sequenceSeekPos: data},'SET_SIM_ANGLES',p)
		default: return state
	}
}
