import React,{useContext} from 'react'

import './BottomDrawer.scss'
import {MainContext} from '../../contexts/MainContext'
import HistoryTimeline from '../HistoryTimeline/HistoryTimeline'
import SequenceControls from '../SequenceControls/SequenceControls'

export default function BottomDrawer(){
	const {state,dispatch}=useContext(MainContext)
	const {showHistoryTimeline,showSequenceControl,showLibrary}=state
	return (
		<div id="BottomDrawer">
			{(showHistoryTimeline || showSequenceControl)===false?<style>:root&#123; --bottomDrawerHeight: 46px &#125;</style>:undefined}
			<div id="taskbar" className="row spc-8 f-end">
				<div className={'button-icon'+(showHistoryTimeline?' active':'')} onClick={()=>dispatch(['SET_STATE',{showHistoryTimeline: !showHistoryTimeline}])}>
					<div className="fa fa-history"/>
					<div className="col">History Timeline</div>
					<div className="fa fa-history"/>
				</div>
				<div className={'button-icon'+(showSequenceControl?' active':'')} onClick={()=>dispatch(['SET_STATE',{showSequenceControl: !showSequenceControl}])}>
					<div className="icon icon-layout_directed"/>
					<div className="col">Sequence Controls</div>
					<div className="icon icon-layout_directed"/>
				</div>
				<div className={'button-icon'+(showLibrary?' active':'')} onClick={()=>dispatch(['SET_STATE',{showLibrary: !showLibrary}])} hidden> {/* TODO: Unhide */}
					<div className="fa fa-bookmark"/>
					<div className="col">Library</div>
					<div className="fa fa-bookmark"/>
				</div>
				<div className="ml-auto h7">App Version: 0.1.7</div>
			</div>
			{showHistoryTimeline?<HistoryTimeline/>:undefined}
			{showSequenceControl?<SequenceControls/>:undefined}
		</div>
	)
}
